@import url(https://fonts.googleapis.com/css2?family=Heebo:wght@400;500;700&display=swap);
body{margin:0;font-family:"Heebo",sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-color:#fff}code{font-family:source-code-pro,Menlo,Monaco,Consolas,"Courier New",monospace}:root{--red: #FF6464;--blue: #00A8CC;--dark: #21243D;--light: #8695A4}
.App{max-width:1000px;margin:auto;padding:0px 24px}
.contact-box h1{margin-top:40px}.contact-box ul{margin-top:40px;list-style-type:none;display:flex;justify-content:space-between;margin:80px 0 80px;padding:0px 40px}.contact-box ul img{width:40px;color:var(--dark)}
.footer{text-align:center;color:var(--dark)}.footer img{width:20px}
.header{position:fixed;top:0px;right:0px;background-color:#fff;width:100%}.navbar ul{list-style-type:none;display:flex;padding:0px 50px;justify-content:flex-end}.navbar ul li{margin-left:32px}.navbar a{text-decoration:none;color:#000;font-weight:500;font-size:20px}
.hero-box{display:flex;justify-content:space-between;margin-top:160px;align-items:center}.responsive{display:none}.hero-text{color:var(--dark)}.hero-text h1{font-weight:700;font-size:44px}.hero-text p{font-size:16px;margin:29px 0}.hero-text button{background-color:var(--red);border:none;border-radius:2px;font-size:20px;font-family:"Heebo",sans-serif;color:#fff;padding:10px 20px;cursor:pointer}.hero-image{padding-left:160px}.hero-image img{max-height:240px;border-radius:50%;box-shadow:-10px 15px 5px #edf7fa}@media(max-width: 700px){.hero-box{margin-top:100px;text-align:center;flex-direction:column}.responsive{display:block;padding:0}.responsive img{max-height:200px}.desktop{display:none}.hero-text h1{font-size:32px}}
.project-list{margin-top:40px}.project-box{display:flex;align-items:center;min-height:180px}.project-box img{width:246px;border-radius:6px;border:1px #e0e0e0 solid}.project-box h1{margin:0px}.project-box a{text-decoration:none;color:#21243d;font-weight:bold;font-size:30px}.project-box p{color:#21243d;font-size:16px}.project-text{display:flex;flex-direction:column;justify-content:space-between;margin-left:20px}.line{width:100%}.button-styled{background-color:var(--red);border:none;border-radius:2px;font-size:16px;font-family:"Heebo",sans-serif;color:#fff;padding:5px 10px;cursor:pointer;margin-right:10px}@media(max-width: 700px){.project-box{flex-direction:column;min-height:300px;text-align:center;padding:50px 0}.line{margin:0}}
.skills-box{margin-top:80px}.skills-box ul{display:flex;list-style-type:none;justify-content:space-between;text-align:center;margin:32px 0px;padding:0px 40px}.skills-box li{margin:20px}.section-title{font-size:22px}@media(max-width: 850px){.skills-box ul{display:grid;grid-template-columns:1fr 1fr 1fr 1fr}.skills-box li{margin:0px}}@media(max-width: 450px){.skills-box ul{display:grid;grid-template-columns:1fr 1fr}.skills-box li{margin:0px}}
