.project-list {
  margin-top: 40px;
}

.project-box {
  display: flex;
  align-items: center;
  min-height: 180px;

  img {
    width: 246px;
    border-radius: 6px;
    border: 1px #e0e0e0 solid;
  }

  h1 {
    margin: 0px;
  }

  a {
    text-decoration: none;
    color: #21243d;
    font-weight: bold;
    font-size: 30px;
  }

  p {
    color: #21243d;
    font-size: 16px;
  }
}

.project-text {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-left: 20px;
}

.line {
  width: 100%;
}

.button-styled {
  background-color: var(--red);
  border: none;
  border-radius: 2px;
  font-size: 16px;
  font-family: 'Heebo', sans-serif;
  color: white;
  padding: 5px 10px;
  cursor: pointer;
  margin-right: 10px;
}

@media (max-width: 700px) {
  .project-box {
    flex-direction: column;
    min-height: 300px;
    text-align: center;
    padding: 50px 0;
  }

  .line {
    margin: 0;
  }
}
